.Login {
    &.wrapper {
        position: relative;
        top: 0;
        font-family: sans-serif;
        background: url(../assets/login.jpg);
        background-size: cover;
        background-position-y: bottom;
    }

    .box {
        width: 400px;
        padding: 40px;
        background: rgba(0, 0, 0, 0.8);
        box-sizing: border-box;
        box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
        border-radius: 10px;
        position: absolute;
        top: 10%;
        right: 10%;

        .link {
            cursor: pointer;
            color: $secondary;
        }

        .title {
            margin: 0 0 30px;
            padding: 0;
            color: $secondary;
            font-size: 30px;
            text-align: center;
        }

        .inputBox {
            position: relative;

            input {
                width: 100%;
                padding: 10px 15px;
                border-radius: 10px;
                font-size: 16px;
                margin-bottom: 30px;
                color: #ffffff;
                border: none;
                border-bottom: 1px solid #fff;
                outline: none;
                background: transparent;
                &:focus ~ label,
                &:valid ~ label {
                    top: -35px;
                    left: 0;
                    color: $secondary;
                    font-size: 12px;
                }
            }

            label {
                position: absolute;
                top: 0;
                left: 0;
                padding: 10px 0;
                font-size: 16px;
                color: #fff;
                pointer-events: none;
                transition: 0.5s;
            }
        }
    }
    .submit,
    .google {
        position: relative;
        border-radius: 10px;
        padding: 10px 0;
        width: 100%;
        font-size: 13px;
        letter-spacing: 2px;
        text-decoration: none;
        text-transform: uppercase;
        overflow: hidden;
        margin-bottom: 10px;
    }
}
